import React, { useEffect } from 'react'

const Toolbar = (props) => {

    const toogleContrast = (init) => {
        if (window.sessionStorage) {
            const contrast = "contrast"
            const current = window.sessionStorage.getItem(contrast)
            window.sessionStorage.setItem(contrast, init && current === undefined ? false : !JSON.parse(current || true))
        }
    }

    useEffect(() => {
        toogleContrast(true)
    }, [])

    return (
        <React.Fragment>
            <div className="app-toolbar">
                <div className="app-toolbar--item">A+</div>
                <div className="app-toolbar--item">A-</div>
                <div className="app-toolbar--item" onClick={() => toogleContrast(false)}>Wersja kontrastowa</div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar